
/* 
    object-fit
    이미지, 비디오 등의 요소의 크기 조절 및 위치를 지정하는데 사용
    
    fill
    - 요소의 크기를 조절하여 부모 요소에 딱 맞도록 합니다. 
    - 원본 비율을 무시하고 요소가 왜곡될 수 있습니다.

    contain
    - 요소를 부모 요소에 맞게 조절합니다.
    - 원본 비율을 유지하면서 요소를 부모 요소에 들어가도록 합니다.
    - 이미지나 비디오가 왜곡되지 않습니다.

    cover
    - 요소를 부모 요소에 가득 채우도록 조절합니다.
    - 부모 요소의 가로 또는 세로 중 하나가 완전히 채워지고,
      다른 쪽은 잘릴 수 있습니다.
    - 원본 비율을 유지하면서 요소가 왜곡될 수 있습니다.

    none
    - 요소가 크기 조절되지 않고, 원본 크기대로 표시됩니다.
    - 부모 요소의 크기를 무시하고 원본 크기로 표시됩니다.

    scale-down
    - contain과 none 중에서 작은 쪽을 적용합니다. 
    - 원본 비율을 유지하면서 요소를 부모 요소에 맞게 조절합니다. 
    - 요소가 원본 크기보다 작은 경우 none과 동일하게 동작합니다.
*/
/* 
    rem
    - 루트 요소의 크기에 상대적으로 설정
    - 루트 요소의 크기와 동일한 크기

    루트 요소
    - <html> / 부모
*/
/* 
    !important
    - 특정 속성 값에 우선순위를 부여하는 데 사용
    - 우선적으로 적용되어야 하는 스타일을 지정할 수 있다.
*/
/* 
    max-width
    - 요소의 최대 너비를 설정하여 자동적으로 브라우저 크기에 따라
      요소의 너비를 조절합니다.
    - 반응형 디자인을 쉽게 구현하고 레이아웃을 제어하고 유연성을
      높일 수 있다.
*/
/* 
    flex-direction
    - 플렉스 속성내의 아이템들이 배치되는 방향을 지정
    row
    - 플렉스 아이템들을 가로형 좌 -> 우로 배치 수평

    row-reverse
    - row와 반대 우 -> 좌로 배치

    column
    - 플렉스 아이템들을 위에서 아래로 배치 수직
    
    column-reverse
    - column과 반대로 아래에서 위로 배치
*/

.banner {
    color: white;
    object-fit: contain;
    height: 448px;
    /* border: 1px solid white; */
}

/* 반응형 */
/* 해상도에 따라 스타일 지정 가능 */
@media (min-width: 1500px) {
    .banner {
        position: relative;
        height: 600px;
    }

    .banner--fadeBottom {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 40rem;
    }
}

@media (max-width: 768px) {
    .banner__contents {
        width: min-content !important;
        padding-left: 2.3rem;
        margin-left: 0px !important;
    }

    .banner__description {
        font-size: 0.8rem !important;
        width: auto !important;
    }

    .info {
        text-align: start;
        padding-right: 1.2rem;
    }

    .space {
        margin-left: 6px;
    }

    .banner_button {
        font-size: 0.8rem !important;
        border-radius: 4px !important;
    }
}

.banner__contents {
    margin-left: 40px;
    padding-top: 140px;
    height: 190px;
    /* border: 1px solid white; */
}

.banner__title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.5rem;
    /* border: 1px solid white; */
}

.banner__description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-weight: 550;
    font-size: 1rem;
    max-width: 400px;
    height: 80px;
    /* border: 1px solid white; */
}

.banner--fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(180deg,
            transparent,
            rgba(37, 37, 37, 0.61),
            #111);
    /* border: 1px solid white; */
}

.banner__buttons {
    display: flex;
    flex-direction: row;
    /* border: 1px solid white; */
}

.banner__button {
    display: flex;
    justify-content: start;
    align-items: center;
    cursor: pointer;
    outline: none;
    border: none;
    font-size: 1rem;
    font-weight: 700;
    padding: 0.4rem 1rem;
    margin-right: 1rem;
}
/* 
    button에 마우스를 올렸을 때
*/
.banner__button:hover {
    color: #000;
    background-color: rgba(170, 170, 170, 0.9);
    transition: all 0.2s;
}

.play {
    background-color: white;
    color: black;
    /* border: 1px solid white; */
}
